<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="studioEditor">
        <el-card>
            <el-form class="u-edit-from" :model="ruleForm" :rules="rules" label-width="160px">
                <div class="multiple-title-card">
                    <div class="title">工作室基本信息</div>
                    <div class="flex-row">
                        <el-form-item label="工作室名称：" prop="studioName">
                            <el-input v-model="ruleForm.name"  placeholder="请输入工作室名称"></el-input>
                        </el-form-item>
                        <el-form-item label="注册手机号：" prop="registPhone">
                            <el-input v-model="ruleForm.tel"  placeholder="请输入注册手机号"></el-input>
                        </el-form-item>
                    </div>
                    <div class="flex-row">
                        <el-form-item label="负责人姓名：" prop="ctrlPerson">
                            <el-input v-model="ruleForm.personName" 
                                      placeholder="请输入负责人姓名"></el-input>
                        </el-form-item>
                        <el-form-item label="联系手机号：" prop="callPhone">
                            <el-input v-model="ruleForm.phone"  placeholder="请输入联系手机号"></el-input>
                        </el-form-item>
                    </div>
                    <el-form-item label="团队介绍：" prop="teamIntroduction">
                        <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}"
                                  v-model="ruleForm.teamIntroduction" style="width: 740px"
                                  placeholder="请输入团队介绍,字数限制100"></el-input>
                    </el-form-item>
                    <el-form-item label="擅长领域：" prop="goodField">
                        <el-checkbox-group v-model="ruleForm.checkList">
                            <el-checkbox label="复选框 A"></el-checkbox>
                            <el-checkbox label="复选框 B"></el-checkbox>
                            <el-checkbox label="复选框 C"></el-checkbox>
                            <el-checkbox label="复选框 d"></el-checkbox>
                            <el-checkbox label="复选框 e"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="工作经历：" prop="workExperience">
                        <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" v-model="ruleForm.work"
                                  style="width: 740px" placeholder="请输入工作经历，字数限制500"></el-input>
                    </el-form-item>
                    <el-form-item label="申请优势：" prop="applyAdvance">
                        <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" v-model="ruleForm.advance"
                                  style="width: 740px" placeholder="请输入申请优势(任职、项目、活动组织经历)，字数限制100"></el-input>
                    </el-form-item>
                    <el-form-item label="运营思路：" prop="thinking">
                        <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" v-model="ruleForm.thinking"
                                  style="width: 740px" placeholder="请输入您理解该地区1-2条'护士工作室'运营思路，字数限制100"></el-input>
                    </el-form-item>
                    <el-form-item label="对护士加理解：" prop="understand">
                        <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" v-model="ruleForm.understand"
                                  style="width: 740px" placeholder="简单描述你对护联网护士加的理解，字数限制500"></el-input>
                    </el-form-item>
                    <el-form-item label="所在地区：" prop="belongArea">
                        <CityCascader v-model="ruleForm.belongDistrict" style="display: inline-block"></CityCascader>
                    </el-form-item>

                </div>
                <div class="multiple-title-card">
                    <div class="title">工作室认证信息</div>
                    <el-form-item label="上传身份证：" prop="idCard">
                        <div>263647563525345621</div>
                        <div class="upload-images">
                            <el-upload
                                class="upload-item"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :show-file-list="false"
                                :on-success="uploadImage1">
                                <img v-if="imageUrl1" :src="imageUrl1" class="img_upload">
                                <i v-else class="el-icon-plus icon_upload"></i>
                            </el-upload>
                            <el-upload
                                class="upload-item"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :show-file-list="false"
                                :on-success="uploadImage2">
                                <img v-if="imageUrl2" :src="imageUrl2" class="img_upload">
                                <i v-else class="el-icon-plus icon_upload"></i>
                            </el-upload>
                        </div>
                    </el-form-item>
                    <el-form-item label="护士执业认证：" prop="Practising">
                        <div><span>3221132345432</span><span class="practise_date">获证日期：2019-4-28</span></div>
                        <div class="upload-images">
                            <el-upload
                                class="upload-item"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :show-file-list="false"
                                :on-success="uploadImage3">
                                <img v-if="imageUrl3" :src="imageUrl3" class="img_upload">
                                <i v-else class="el-icon-plus icon_upload"></i>
                            </el-upload>
                        </div>
                    </el-form-item>
                    <el-form-item label="护士资质认证：" prop="intelligence">
                        <div>22222233333333</div>
                        <div class="upload-images">
                            <el-upload
                                class="upload-item"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :show-file-list="false"
                                :on-success="uploadImage4">
                                <img v-if="imageUrl4" :src="imageUrl4" class="img_upload">
                                <i v-else class="el-icon-plus icon_upload"></i>
                            </el-upload>
                        </div>
                    </el-form-item>
                    <el-form-item label="工作室头像：" prop="avatar">
                        <div class="upload-images">
                            <el-upload
                                class="upload-item"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :show-file-list="false"
                                :on-success="uploadImage5">
                                <img v-if="imageUrl5" :src="imageUrl5" class="img_upload">
                                <i v-else class="el-icon-plus icon_upload"></i>
                            </el-upload>
                        </div>
                    </el-form-item>
                </div>
                <div>
                    <!--                修改页面和新增页面-->
                    <el-button type="primary" v-if="isAddPage">提交审核</el-button>
                    <el-button type="primary" v-else>提交修改</el-button>
                </div>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    import CityCascader from '@/components/CityCascader/CityCascader'

    export default {
        data() {
            return {
                isAddPage: false,
                ruleForm: {
                    checkList: [],
                    belongDistrict: []
                },
                rules: {
                    studioName: [
                        { required: true }
                    ],
                    registPhone: [
                        { required: true }
                    ],
                    ctrlPerson: [
                        { required: true }
                    ],
                    callPhone: [
                        { required: true }
                    ],
                    teamIntroduction: [
                        { required: true }
                    ],
                    goodField: [
                        { required: true }
                    ],
                    workExperience: [
                        { required: true }
                    ],
                    applyAdvance: [
                        { required: true }
                    ],
                    thinking: [
                        { required: true }
                    ],
                    understand: [
                        { required: true }
                    ],
                    belongArea: [
                        { required: true }
                    ],
                    idCard: [
                        { required: true }
                    ]

                },
                imgSrc: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
                imageUrl1: '',
                imageUrl2: '',
                imageUrl3: '',
                imageUrl4: '',
                imageUrl5: '',

            }
        },
        methods: {
            uploadImage1(res, file) {
                this.imageUrl1 = URL.createObjectURL(file.raw);
            },
            uploadImage2(res, file) {
                this.imageUrl2 = URL.createObjectURL(file.raw);
            },
            uploadImage3(res, file) {
                this.imageUrl3 = URL.createObjectURL(file.raw);
            },
            uploadImage4(res, file) {
                this.imageUrl4 = URL.createObjectURL(file.raw);
            },
            uploadImage5(res, file) {
                this.imageUrl5 = URL.createObjectURL(file.raw);
            }
        },
        created() {
            if (this.$route.name === 'StudioAdd') {
                this.isAddPage = true
            }
        },
        components: {
            CityCascader
        }
    }
</script>
